import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthenticationService } from '../services/authentication.service';
import {MatSnackBar} from '@angular/material';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  protected user = {
    email: '',
    password: ''
  };

  private email = 'admin@ufcg.edu.br';
  private password = 'transportesufcg';

  constructor(private router: Router,
              private authenticationService: AuthenticationService,
              private snackBar: MatSnackBar) { }

  ngOnInit() {
  }

  login(): void {
    //this.authenticationService.login(this.user).subscribe(token => {
    //  console.log(token);
    //}, error => {
    //  console.log(error)
    //});
    if (this.password === this.user.password && this.email === this.user.email) {
      this.router.navigate(['home']);
    } else {
      const snackBarRef = this.snackBar.open('EMAIL OU SENHA NÃO CONFEREM!', 'OK', {duration: 5000});
      this.user.password = '';
    }
  }

}



